<template>
  <div class="flex">
    <div class="fixedGlobal">
      <GlobalHeader />
    </div>
    <div class="wrap">
      <h1></h1>
      <div class="innerWrap">
        <p class="title"></p>
        <p class="error" v-if="titleError"></p>
        <input v-model="title" type="text" class="text" maxlength="40" />
        <br />
        <p class="title"><small id="modalOpen" class="modalOpen" @click="OpneModal()"></small><span class="space">,</span></p>
        <p class="error" v-if="keyError"></p>
        <input
          v-model="keyword"
          type="text"
          class="text"
          maxlength="255"
          placeholder="aaa,bbbccc,ddd"
        />
        <br />
        <p class="title"></p>
        <p class="error" v-if="textError"></p>
        <textarea
          v-model="text_uri"
          maxlength="255"
          name=""
          cols="30"
          rows="10"
          class="textarea"
        ></textarea>
        <br />
        <!-- <button @click="ImageRequest()"></button> -->
        <button @click="ImageRequest"></button>
      </div>
      <template v-if="loadFlag">
        <div class="loadingWrap">
          <div class="loadingContent">
            <p class="warningTitle"></p>
            <p class="warning">2030</p>
            <p class="warning"></p>
            <div>
              <vue-element-loading
                :active="loadFlag.length != 0"
                spinner="bar-fade-scale"
              />
            </div>
          </div>
        </div>
      </template>  
    </div>
    <div class="modalWrap">
      <div id="easyModal" class="modal">
        <div class="modal-content">
          <div class="modal-header">
            <h1></h1>
            <span class="modalClose"></span>
          </div>
          <div class="modal-body">
            <div class="modal-body-block">
              <h2>&#040;&#041;</h2>
              <p><code class="code">&#040;&#041;</code></p>
              <p>&#040;&#041;</p>
              <p>&#040;&#041;23</p>
            </div>
            <hr>
            <div class="modal-body-block">
              <h2>&#091;&#093;</h2>
              <p><code class="code">&#040;&#041;&#091;&#093;</code></p>
              <p>&#091;&#093;</p>
              <p>&#091;&#093;23</p>
            </div>
            <hr>
            <div class="modal-body-block">
              <h2></h2>
              <p></p>
            </div>
            <hr>
            <div class="modal-body-block">
              <h2></h2>
              <p></p>
            </div>
            <hr>
            <div class="modal-body-block">
              <h2></h2>
              <p></p>
            </div>
            <hr>
            <div class="modal-body-block">
              <h3>Stable Diffusion</h3>
            </div>
          </div>
        </div>
      </div>
    </div> 
  </div>
</template>

<script>
import axios from "axios";
import { AI_SERVER } from "@/assets/config.js";
import VueElementLoading from "vue-element-loading";
import GlobalHeader from "@/components/GlobalHeader.vue";

export default {
  data() {
    return {
      loadFlag: false,
      user: localStorage.getItem("id"),
      title: "",
      keyword: "",
      text_uri: "",
      image_uri: "test.png",
      titleError: false,
      keyError: false,
      textError: false,
      keywordAry: [],
    };
  },
  name: "CreateImage",
  components: {
    VueElementLoading,
    GlobalHeader,
  },
  methods: {
    ImageRequest: async function () {
      //
      this.titleError = false;
      this.keyError = false;
      this.textError = false;
      if (this.title == "" || this.keyword == "" || this.text_uri == "") {
        if (this.title == "") {
          this.titleError = true;
        }
        if (this.keyword == "") {
          this.keyError = true;
        }
        if (this.text_uri == "") {
          this.textError = true;
        }
        return;
      }
      //API
      //
      this.loadFlag = true;
      // console.log(this.loadFlag);
      this.keywordAry = this.keyword.split("");
      this.keyword = this.keywordAry.join(",");
      const requestBody = {
        user_id: this.user,
        keyword: this.keyword,
      };
      await axios
        .post(AI_SERVER + "/ai/debug", requestBody)
        .then((response) => {
          //
          this.image_uri = response.data.img_file;

          // 
          localStorage.setItem("title", this.title);
          localStorage.setItem("keyword", this.keyword);
          localStorage.setItem("text_uri", this.text_uri);
          localStorage.setItem("image_uri", this.image_uri);
          //
          this.loadFlag = false;
          this.$router.push("/PreviewImage");
        })
        .catch((e) => {
          console.log(e);
          console.log("");
        });
      //test
      //
      // localStorage.setItem("title", this.title);
      // localStorage.setItem("keyword", this.keyword);
      // localStorage.setItem("text_uri", this.text_uri);
      // localStorage.setItem("image_uri", this.image_uri);
      // console.log(this.keyword);
      // //
      // this.$router.push("/PreviewImage");
      //test end

      this.loadFlag = false;
    },

    // --------------------
    OpneModal() {
      const buttonOpen = document.getElementById('modalOpen');
      const modal = document.getElementById('easyModal');
      const buttonClose = document.getElementsByClassName('modalClose')[0];

      // 
      buttonOpen.addEventListener('click', modalOpen);
      function modalOpen() {
        modal.style.display = 'block';
      }

      // 
      buttonClose.addEventListener('click', modalClose);
      function modalClose() {
        modal.style.display = 'none';
      }

      // 
      addEventListener('click', outsideClose);
      function outsideClose(e) {
        if (e.target == modal) {
          modal.style.display = 'none';
        }
      }
    },
    // --------------------
  },
  created() {
    if (this.$cookies.get("access") === null) {
      const refresh = this.$cookies.get("refresh");
      if (refresh != null) {
        console.log("");
        const requestBody = {
          refresh: refresh,
        };
        axios
          .post(API_SERVER + "/api/v1/auth/jwt/refresh/", requestBody)
          .then((response) => {
            let token = response.data.access;
            this.$cookies.set("access", token, 60 * 30);
            location.reload();
          })
          .catch(() => {
            //
            this.$router.push("/SignIn");
          });
      }else{
        this.$router.push("/SignIn");
      }
      return;
    }
  },
};
</script>

<style scoped>
